    text-align: center; }
    body #root main #footer a {
      border-radius: 5px;
      padding: 10px 80px;
      border: 1px solid;
      -webkit-animation: button-ani 10s infinite;
              animation: button-ani 10s infinite; }

@-webkit-keyframes button-ani {
  25% {
    border-color: red;
    color: red; }
  50% {
    border-color: lime;
    color: lime; }
  75% {
    border-color: blue;
    color: blue; } }

@keyframes button-ani {
  25% {
    border-color: red;
    color: red; }
  50% {
    border-color: lime;
    color: lime; }
  75% {
    border-color: blue;
    color: blue; } }

@media only screen and (max-width: 800px) {
